@import url(https://use.typekit.net/szn0ucq.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'muli-regular';
  src: url(/static/media/Muli-Regular.e063a333.ttf);
}
@font-face {
  font-family: 'muli-bold';
  src: url(/static/media/Muli-Bold.44ea8275.ttf);
}
@font-face {
  font-family: 'gothic-1';
  src: url(/static/media/GothicA1-Bold.f53ebb7b.ttf);
}
*:focus {
  outline: none;
}
body {
  overflow-y:scroll;
}
.main_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  font-family: 'muli-regular';
}
.section_content {
  width: 100%;
  background: #F5F5F5;
}
.module_path span {
  color: #2185d0;
  font-size: 0.8rem;
}
.module_path p {
  color: #1b1c1d;
  font-size: 0.8rem;
}


.register_title {
  font-size: 2rem;
}

.register_form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.register_form > input {
  width: 20vw;
  height: 30px;
  text-align: center;
  font-size: 1rem;
  margin-bottom: 40px;
}
.activationCode {
  -webkit-animation: fadeIn 1s ease-out;
          animation: fadeIn 1s ease-out;
}

.register_form > button {
  width: 100px;
  height: 30px;
  border: solid #909090 2px;
  border-radius: 10px;
  font-size: 0.9rem;
  box-shadow: rgb(218, 217, 217) 0px 0px 20px;
}

.dim {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: none;
}

.section_header {
  background: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 80px;
  box-shadow: #0000000a 0 2px 6px;
}
.header_logo {
  background: url(/static/media/AQOOM_logo.2ada93ac.png) no-repeat;
  background-size: contain;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  width: 250px;
  background-position-y: center;
}
.header_infos {
  display: table;
  width: 220px;
}
.header_infos .header_name {
  display: table-cell;
  width: 50%;
  line-height: 80px;
  font-size: 14px;
  color: #4d4f5c;
  cursor: pointer;
  overflow: hidden;
}
.header_infos .header_photo {
  display: table-cell;
  width: 50%;
  vertical-align: middle;
}
.header_infos .header_photo img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.icon.icon-down {
  width: 15px;
  height: 15px;
  background: url(/static/media/sort-down-solid.d8e48bb5.svg) no-repeat;
  margin-left: 10px;
  display: inline-block;
}

.header_drop {
  display: none;
  width: 140px;
  height: 64px;
  border-radius: 3px;
  box-shadow: #00000038 0 3px 6px;
  position: absolute;
  top: 58px;
  right: 63px;
  z-index: 100;
  list-style: none;
  font-size: 10px;
  color: #00000038;
  flex-direction: column;
  justify-content: space-evenly;
}
.header_drop.open {
  display: flex;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

